===============================\n */\n.mdui-slider {\n  position: relative;\n  display: block;\n  width: 100%;\n  height: 36px;\n}\n.mdui-slider input[type=\"range\"] {\n  position: absolute;\n  top: 50%;\n  z-index: 2;\n  width: 100%;\n  height: 20px;\n  margin-top: -10px;\n  cursor: pointer;\n  opacity: 0;\n  appearance: none;\n}\n.mdui-slider-track,\n.mdui-slider-fill {\n  position: absolute;\n  top: 50%;\n  box-sizing: border-box;\n  height: 2px;\n  margin-top: -1px;\n}\n.mdui-slider-track::before,\n.mdui-slider-fill::before {\n  display: block;\n  width: 100%;\n  height: 100%;\n  content: ' ';\n}\n/* 轨道 */\n.mdui-slider-track {\n  right: 0;\n}\n.mdui-slider-track::before {\n  background-color: #bdbdbd;\n  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n}\n/* 已滑动部分 */\n.mdui-slider-fill {\n  left: 0;\n}\n.mdui-slider-fill::before {\n  background-color: #FF4081;\n}\n/* 滑块 */\n.mdui-slider-thumb {\n  position: absolute;\n  top: 50%;\n  box-sizing: border-box;\n  width: 12px;\n  height: 12px;\n  margin-top: -6px;\n  background-color: #FF4081;\n  border: 2px solid #FF4081;\n  border-radius: 50%;\n  transform: translate(-50%);\n  transition: background 0.45s cubic-bezier(0.4, 0, 0.2, 1), border-color 0.45s cubic-bezier(0.4, 0, 0.2, 1), transform 0.2s cubic-bezier(0.4, 0, 0.2, 1), border-radius 0.2s cubic-bezier(0.4, 0, 0.2, 1);\n  will-change: background, border-color, transform, border-radius;\n}\n/**\n * =============================================================================\n * ************   Slider 强调色   ************\n * =============================================================================\n */\n.mdui-theme-accent-amber .mdui-slider-fill::before {\n  background-color: #FFD740;\n}\n.mdui-theme-accent-amber .mdui-slider-thumb {\n  background-color: #FFD740;\n  border: 2px solid #FFD740;\n}\n.mdui-theme-accent-blue .mdui-slider-fill::before {\n  background-color: #448AFF;\n}\n.mdui-theme-accent-blue .mdui-slider-thumb {\n  background-c