==================================================\n */\n/* 默认为 Flat 扁平按钮 */\n.mdui-btn,\n.mdui-fab {\n  position: relative;\n  display: inline-block;\n  box-sizing: border-box;\n  min-width: 88px;\n  height: 36px;\n  margin: 0;\n  padding: 0 16px;\n  overflow: hidden;\n  color: inherit;\n  font-weight: 500;\n  font-size: 14px;\n  line-height: 36px;\n  letter-spacing: 0.04em;\n  white-space: nowrap;\n  text-align: center;\n  text-transform: uppercase;\n  text-decoration: none;\n  vertical-align: middle;\n  background: transparent;\n  border: none;\n  border-radius: 2px;\n  outline: none;\n  cursor: pointer;\n  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1);\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n  touch-action: manipulation;\n  will-change: box-shadow;\n  zoom: 1;\n  -webkit-user-drag: none;\n}\n.mdui-btn:hover,\n.mdui-fab:hover {\n  background-color: rgba(0, 0, 0, 0.1);\n}\n.mdui-btn:not(.mdui-ripple):active,\n.mdui-fab:not(.mdui-ripple):active {\n  background-color: rgba(0, 0, 0, 0.165);\n}\n.mdui-btn[class*=\"mdui-color-\"]:hover,\n.mdui-fab[class*=\"mdui-color-\"]:hover {\n  opacity: 0.87;\n}\n.mdui-btn:not(.mdui-ripple)[class*=\"mdui-color-\"]:active,\n.mdui-fab:not(.mdui-ripple)[class*=\"mdui-color-\"]:active {\n  opacity: 0.76;\n}\n/* 按钮内的图标 */\n.mdui-btn .mdui-icon-left,\n.mdui-btn .mdui-icon-right,\n.mdui-btn .mdui-icon-left::before,\n.mdui-btn .mdui-icon-right::before {\n  height: inherit;\n  font-size: 1.3em;\n  line-height: inherit;\n}\n.mdui-btn .mdui-icon-left {\n  float: left;\n  margin-right: 0.4em;\n}\n.mdui-btn .mdui-icon-right {\n  float: right;\n  margin-left: 0.4em;\n}\ninput.mdui-btn[type=\"submit\"] {\n  appearance: none;\n}\n/* Raised button 浮动按钮 */\n.mdui-btn-raised {\n  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);\n}\n.mdui-btn-raised:hover {\n  box-shadow: 0 2p