hen (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-display-2-opacity() {\n  opacity: 0.54;\n}\n.mdui-typo-display-2(@opacity: false) {\n  font-weight: 400;\n  font-size: 45px;\n  letter-spacing: 0;\n  & when (@opacity = true) {\n    ._mdui-typo-display-2-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-display-1-opacity() {\n  opacity: 0.54;\n}\n.mdui-typo-display-1(@opacity: false) {\n  font-weight: 400;\n  font-size: 34px;\n  letter-spacing: 0;\n  & when (@opacity = true) {\n    ._mdui-typo-display-1-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-headline-opacity() {\n  opacity: 0.87;\n}\n.mdui-typo-headline(@opacity: false) {\n  font-weight: 400;\n  font-size: 24px;\n  -moz-osx-font-smoothing: grayscale;\n  & when (@opacity = true) {\n    ._mdui-typo-headline-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-title-opacity() {\n  opacity: 0.87;\n}\n.mdui-typo-title(@opacity: false) {\n  font-weight: 500;\n  font-size: 20px;\n  letter-spacing: 0.02em;\n  & when (@opacity = true) {\n    ._mdui-typo-title-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-subheading-opacity() {\n  opacity: 0.87;\n}\n.mdui-typo-subheading(@opacity: false) {\n  font-weight: 400;\n  font-size: 16px;\n  letter-spacing: 0.04em;\n  & when (@opacity = true) {\n    ._mdui-typo-subheading-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-body-2-opacity() {\n  opacity: 0.87;\n}\n.mdui-typo-body-2(@opacity: false) {\n  font-weight: 500;\n  font-size: 14px;\n  letter-spacing: 0.04em;\n  & when (@opacity = true) {\n    ._mdui-typo-body-2-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-body-1-opacity() {\n  opacity: 0.87;\n}\n.mdui-typo-body-1(@opacity: false) {\n  font-weight: 400;\n  font-size: 14px;\n  letter-spacing: 0.04em;\n  & when (@opacity = true) {\n    ._mdui-typo-body-1-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n\n._mdui-typo-caption-opacity() {\n  opacity: 0.54;\n}\n.mdui-typo-caption(@opacity: false) {\n  font-weight: 400;\n  font-size: 12px;\n  letter-spacing: 0.08em;\n  & when (@opacity = true) {\n    ._mdui-typo-caption-opacity();\n  }\n  & when (isnumber(@opacity)) {\n    opacity: @opacity;\n  }\n}\n","/**\n * =============================================================================\n * ************   Typography 文章排版   ************\n * =============================================================================\n */\n\n.mdui-typo {\n  line-height: 1.8;\n  word-wrap: break-word;\n\n  address, caption, cite, code, dfn, th {\n    font-weight: 400;\n    font-style: normal;\n  }\n\n  caption, th {\n    text-align: left;\n  }\n\n  q::before, q::after {\n    content: '';\n  }\n\n  pre, code, kbd, samp, pre tt {\n    font-family: Consolas, Courier, 'Courier New', monospace;\n  }\n\n  figcaption {\n    color: @color-black-secondary;\n    font-size: 80%;\n  }\n\n  [draggable],\n  [draggable=\"true\"] {\n    cursor: move;\n  }\n\n  [draggable=\"false\"] {\n    cursor: inherit;\n  }\n\n  p, pre, ul, ol, dl, form, hr, figure, table, .mdui-table, .mdui-table-fluid {\n    margin: 0 0 1.2em 0;\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n\n  /* a */\n  a {\n    .mdui-text-color(@color-accent-default-name, a200);\n\n    position: relative;\n    display: inline-block;\n    overflow: hidden;\n    text-decoration: none;\n    vertical-align: top;\n    outline: none;\n\n    &::before {\n      position: absolute;\n      top: auto;\n      bottom: 1px;\n      left: 0;\n      width: 100%;\n      height: 1px;\n      background-color: @color-default-a200;\n      transform: scaleX(0);\n      backface-visibility: hidden;\n      transition: all 0.2s;\n      content: ' ';\n    }\n\n    &:hover::before,\n    &:focus: