n\n      footer {\n        color: @color-white-secondary;\n      }\n    }\n\n    figcaption {\n      color: @color-white-secondary;\n    }\n\n    mark {\n      background: #aaa;\n      border-bottom-color: #bbb;\n    }\n\n    h1, h2, h3, h4, h5, h6 {\n      small {\n        color: @color-white-secondary;\n      }\n    }\n\n    code {\n      color: @color-red-100;\n      background-color: #424242;\n    }\n\n    pre {\n      background: #424242;\n      border-color: @color-white-divider;\n    }\n\n    kbd {\n      background: #424242;\n    }\n\n    hr {\n      border-color: @color-white-divider;\n    }\n  }\n});\n","/**\n * =============================================================================\n * ************   Grid 网格系统   ************\n * =============================================================================\n */\n\n@_screen-col-1: 8.333333%;\n@_screen-col-2: 16.666667%;\n@_screen-col-3: 25%;\n@_screen-col-4: 33.333333%;\n@_screen-col-5: 41.666667%;\n@_screen-col-6: 50%;\n@_screen-col-7: 58.333333%;\n@_screen-col-8: 66.666667%;\n@_screen-col-9: 75%;\n@_screen-col-10: 83.333333%;\n@_screen-col-11: 91.666667%;\n@_screen-col-12: 100%;\n\n// 等分列\n@_screen-average-1: 100%;\n@_screen-average-2: 50%;\n@_screen-average-3: 33.333333%;\n@_screen-average-4: 25%;\n@_screen-average-5: 20%;\n@_screen-average-6: 16.666667%;\n@_screen-average-7: 14.285714%;\n@_screen-average-8: 12.5%;\n@_screen-average-9: 11.111111%;\n@_screen-average-10: 10%;\n@_screen-average-11: 9.090909%;\n@_screen-average-12: 8.333333%;\n\n//间隔\n@grid-gap-8: 8px;\n@grid-gap-16: 16px;\n@grid-gap-24: 24px;\n@grid-gap-40: 40px;\n\n// 生成列、列偏移\n._grid_col_loop(@cols, @screen) when (@cols > 0) {\n  ._grid_col_loop((@cols - 1), @screen);\n\n  @widthName: '_screen-col-@{cols}';\n\n  // 列宽度\n  .mdui-col-@{screen}-@{cols} {\n    float: left;\n    width: @@widthName;\n  }\n\n  // 列偏移\n  .mdui-col-offset-@{screen}-@{cols} {\n    margin-left: @@widthName;\n  }\n\n  // 等分列\n  @averageName: '_screen-average-@{cols}';\n  .mdui-row-@{screen}-@{cols} .mdui-col {\n    float: left;\n    width: @@averageName;\n  }\n}\n\n.mdui-container,\n.mdui-container-fluid {\n  box-sizing: border-box;\n  margin-right: auto;\n  margin-left: auto;\n  padding-right: @grid-gap-16 / 2;\n  padding-left: @grid-gap-16 / 2;\n  &::after{\n    display: table;\n    clear: both;\n    content: '';\n  }\n}\n\n.mdui-container {\n  width: 96%;\n  max-width: 1280px;\n  @media (min-width: @screen-sm-min) {\n    width: 94%;\n  }\n  @media (min-width: @screen-md-min) {\n    width: 92%;\n  }\n}\n\n.mdui-row,\n[class*=\"mdui-row-\"] {\n  margin-right: -@grid-gap-16 / 2;\n  margin-left: -@grid-gap-16 / 2;\n  &::after {\n    display: table;\n    clear: both;\n    content: '';\n  }\n}\n\n[class*=\"mdui-col-xs-\"],\n[class*=\"mdui-col-sm-\"],\n[class*=\"mdui-col-md-\"],\n[class*=\"mdui-col-lg-\"],\n[class*=\"mdui-col-xl-\"],\n.mdui-col {\n  position: relative;\n  box-sizing: border-box;\n  min-height: 1px;\n  padding-right: @grid-gap-16 / 2;\n  padding-left: @grid-gap-16 / 2;\n}\n\n/* 取消列间距 */\n.mdui-row-gapless {\n  .mdui-col,\n  [class*=\"mdui-col-xs-\"],\n  [class*=\"mdui-col-sm-\"],\n  [class*=\"mdui-col-md-\"],\n  [class*=\"mdui-col-lg-\"],\n  [class*=\"mdui-col-xl-\"] {\n    padding-right: 0;\n    padding-left: 0;\n  }\n\n  // 嵌套元素的内补\n  .mdui-row,\n  [class*=\"mdui-row-\"] {\n    margin-right: 0;\n    margin-left: 0;\n  }\n}\n\n._grid_col_loop(12, ~\"xs\");\n\n@media (min-width: @screen-sm-min) {\n  ._grid_col_loop(12, ~\"sm\");\n}\n\n@media (min-width: @screen-md-min) {\n  ._grid_col_loop(12, ~\"md\");\n}\n\n@media (min-width: @screen-lg-min) {\n  ._grid_col_loop(12, ~\"lg\");\n}\n\n@media (min-width: @screen-xl-min) {\n  ._grid_col_loop(12, ~\"xl\");\n}","// 阴影\n.mdui-shadow(@depth) {\n  @shadow: 'shadow-depth-@{depth}';\n\n  box-shadow: @@shadow;\n}\n\n// hover 时添加阴影\n.mdui-hoverable(@depth: 8) {\n  transition: box-shadow 0.25s @animation-curve-default;\n  will-change: box-shadow;\n\n  &:hover,\n  &:focus {\