==\n */\n\n .mdui-collapse-item-header {\n  // 图标，打开时翻转 180 度\n  .mdui-collapse-item-arrow,\n  &.mdui-collapse-item-arrow {\n    transform: rotate(0);\n    transition: transform .3s @animation-curve-default;\n    will-change: transform;\n  }\n}\n\n.mdui-collapse-item-body {\n  height: 0;\n  margin-top: 0;\n  margin-bottom: 0;\n  padding-top: 0;\n  padding-bottom: 0;\n  overflow: hidden;\n  transition: all .3s @animation-curve-default;\n  will-change: height;\n\n  // 使子列表缩进\n  .mdui-list-item {\n    padding-left: 72px;\n  }\n}\n\n.mdui-collapse-item-open {\n  &>.mdui-collapse-item-header {\n    .mdui-collapse-item-arrow,\n    &.mdui-collapse-item-arrow {\n      transform: rotate(180deg);\n    }\n  }\n\n  &>.mdui-collapse-item-body {\n    height: auto;\n  }\n}\n","/**\n * =============================================================================\n * ************   Expansion panel 可扩展面板   ************\n * =============================================================================\n */\n\n/* 可扩展面板 */\n.mdui-panel {\n  box-sizing: border-box;\n  width: 100%;\n}\n\n/* 面板项 */\n.mdui-panel-item {\n  color: @color-black-text;\n  background-color: #fff;\n  transition: margin .3s @animation-curve-default;\n  will-change: margin;\n  .mdui-divider-bottom-dark();\n  .mdui-shadow(2);\n\n  &:last-child {\n    border-bottom: none;\n  }\n}\n\n/* 面板头部 */\n.mdui-panel-item-header {\n  position: relative;\n  display: flex;\n  align-items: center;\n  box-sizing: border-box;\n  width: 100%;\n  height: 48px;\n  padding: 0 24px;\n  font-size: 15px;\n  cursor: pointer;\n  transition: all .3s @animation-curve-default;\n  will-change: height, background-color;\n  .mdui-text-truncate();\n\n  &:active {\n    background-color: @color-grey-200;\n  }\n}\n\n/* 面板项标题 */\n.mdui-panel-item-title {\n  box-sizing: border-box;\n  width: 36%;\n  min-width: 36%;\n  padding-right: 16px;\n  font-weight: 500;\n  .mdui-text-truncate();\n}\n\n/* 面板项内容 */\n.mdui-panel-item-summary {\n  flex-grow: 1;\n  box-sizing: border-box;\n  padding-right: 16px;\n  color: @color-black-secondary;\n  .mdui-text-truncate();\n}\n\n/* 展开收起的图标 */\n.mdui-panel-item-arrow {\n  position: absolute;\n  top: 12px;\n  right: 24px;\n  color: @color-black-icon;\n  transform: rotate(0);\n  transition: all .3s @animation-curve-default;\n  user-select: none;\n  will-change: transform, top;\n}\n\n.mdui-panel-item-summary + .mdui-panel-item-arrow {\n  position: relative;\n  top: 0 !important;\n  right: 0;\n}\n\n/* 面板项内容 */\n.mdui-panel-item-body {\n  height: 0;\n  padding: 0 24px;\n  overflow: hidden;\n  transition: height .3s @animation-curve-default;\n  will-change: height;\n  .mdui-clearfix();\n\n  &::after {\n    height: 16px;\n  }\n}\n\n/* 面板项操作栏 */\n.mdui-panel-item-actions {\n  display: block;\n  width: 100%;\n  margin: 16px -24px 0 -24px;\n  padding: 16px 24px 0 24px;\n  text-align: right;\n  .mdui-divider-top-dark();\n\n  .mdui-btn {\n    margin-left: 8px;\n\n    &:first-child {\n      margin-left: 0;\n    }\n  }\n}\n\n/* 打开状态的面板项 */\n.mdui-panel-item-open {\n  height: auto;\n  margin-top: 16px;\n  margin-bottom: 16px;\n\n  // 打开状态的面板头部更高\n  &>.mdui-panel-item-header {\n    height: 64px;\n\n    .mdui-panel-item-arrow {\n      top: 20px;\n      transform: rotate(180deg);\n    }\n  }\n\n  &>.mdui-panel-item-body {\n    height: auto;\n  }\n}\n\n/* 使打开项上下没有边距 */\n.mdui-panel-gapless {\n  .mdui-shadow(2);\n\n  .mdui-panel-item {\n    .mdui-shadow(0);\n  }\n\n  .mdui-panel-item-open {\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n}\n\n/* 弹出面板 */\n.mdui-panel-popout {\n  .mdui-panel-item-open {\n    margin-right: -16px;\n    margin-left: -16px;\n  }\n}\n\n\n/**\n * =============================================================================\n * ************   Expansion panel dark   ************\n * =============================================================================\n */\n.layout-theme({\n  .mdui-panel-item {\n    color: #fff;\n    background-color: @layout-dark-color-3;\n    .mdui-divider-bottom-light();\n\n    &:last-child {\n      border-bottom: none;\n    }\n  }\n  .mdui-panel-item-header {\n    &:active {\n      background-color: @color-grey-800;\n    }\n  }\n  .mdui-panel-item-summary {\n    color: @color-white-secondary;\n  }\n\n  /* stylelint-disable-next-line */\n  .mdui-panel-item-arrow {\n    color: @color-white-icon;\n  }\n  .mdui-panel-item-actions {\n    .mdui-divider-top-light();\n  }\n});\n","// 分割线\n// =============================================================================\n\n// 添加边框作为分割线，亮色的分割线，用于深色背景\n.mdui-divider-top-light() {\n  border-top: 1px solid @color-white-divider;\n}\n.mdui-divider-bottom-light() {\n  border-bottom: 1px solid @color-white-divider;\n}\n.mdui-divider-left-light() {\n  border-left: 1px solid @color-white-divider;\n}\n.mdui-divider-right-light() {\n  border-right: 1px solid @color-white-divider;\n}\n\n// 添加边框作为分割线，深色的分割线，用于浅色背景\n.mdui-divider-top-dark() {\n  border-top: 1px solid @color-black-divider;\n}\n.mdui-divider-bottom-dark() {\n  border-bottom: 1px solid @color-black-divider;\n}\n.mdui-divider-left-dark() {\n  border-left: 1px solid @color-black-divider;\n}\n.mdui-divider-right-dark() {\n  border-right: 1px solid @color-black-divider;\n}\n","/**\n * =============================================================================\n * ************   Table 表格   ************\n * =============================================================================\n */\n\n@import '../selection_control/checkbox';\n\n.mdui-table {\n  position: relative;\n  width: 100%;\n  background-color: #fff;\n  border: 1px solid @color-black-divider;\n  border-bottom: none;\n  border-collapse: separate;\n  border-spacing: 0;\n  .mdui-shadow(2);\n\n  tbody {\n    tr {\n      position: relative;\n      transition: background-color 0.28s @animation-curve-default;\n    }\n  }\n\n  th, td {\n    position: relative;\n    box-sizing: border-box;\n    padding: 12px 28px;\n    text-align: left;\n    vertical-align: middle;\n    .m